.widget-sm{
    flex: 1;
    -webkit-box-shadow: 0 0 15px -10px rgba(0, 0, 0, 0.75 );
    box-shadow: 0 0 15px -10px rgba(0, 0, 0, 0.75 );
    padding: 20px;
    margin-right: 20px;
}

.widget-smtitle{
    font-size: 22px;
    font-weight: 600;
}

.widget-sm-img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.widget-sm-list{
    margin: 0;
    padding: 0;
    list-style: none;
}

.widget-sm-listitem{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0;
}

.widget_sm-user{
    display: flex;
    flex-direction: column;
}

.widget_sm-username{
    font-weight: 600;
}

.widget_sm-usertitle{
    font-weight: 300;
}

.widget_sm-button{
    display: flex;
    align-items: center;
    border: none;
    border-radius: 5px;
    padding: 7px 10px;
    background-color: #eeeef7;
    color: #555;
    cursor: pointer;
}

.widget_sm-icon{
    font-size: 16px!important;
    margin-right: 5px;
}